<template>
    <div>
        <strong>chatting test</strong>
        <div class="chatting-area">
            <input type="text" v-model="message">
            <button @click="send"></button>
        </div>
        <div class="receive-area-wrapper">
           <div class="receive-area">
                <ul class="receive-area-ul">
                    <!-- TODO:  class   -->
                    <li v-for="(item, index) in receiveMsg" :key="index" :class="{al :  == item., ar : != item.}">
                        <div>
                            <strong :class="{colored: item. == this.}">{{item. || ''}} :</strong> {{item.}}
                        </div>
                    </li>
                </ul>
           </div>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            message: '',
            websocket: null,
            receiveMsg: [],
            : ''
        }
    },
    mounted() {
        this.setWebSocket();
        this. = localStorage.getItem('id');
    },
    methods: {
        setWebSocket() {
            // websocket 
            this.websocket = new WebSocket("ws://tomhoon.duckdns.org:18080/ws/chat");
            this.websocket.onmessage = this.onMessage;	//    
            this.websocket.onopen = this.onOpen;		//  ( )
        },
        onClose(evt) {
            console.log(">>> close event", evt);
        },  
        onOpen(evt) {
            console.log(">>> open event ", evt);
        },
        //   
        onMessage(msg) {
            console.log(">>> msg ", msg.data);
            let { chatId, chatContent }= JSON.parse(msg.data);

            let  = {
                : chatId,
                : chatContent
            };

            this.receiveMsg.push();
        },
        send() {
            let message = this.message;

            if (message != "") {
                let msg = {
                    chatId: this.,
                    chatContent: message
                }

                if (message != null) {
                    this.websocket.send(JSON.stringify(msg));
                }
                
            }
            this.message = '';
        }
    },
    destroyed() {
        this.websocket.onclose = this.onClose;	//  ( )
    }
}
</script>
<style scoped>
    .receive-area-wrapper {
        width: 100%;
        padding:20px;
    }
    .receive-area {
        margin: 0 auto;
        border: 1px solid black;
        border-radius: 10px;
        width: 500px;
        height: 500px;
        background:white;
    }
    .receive-area-ul {
        list-style: none;
    }
    .al {
        text-align: left;
        margin-left: 10px;
    }
    .ar {
        text-align: right;
        margin-right: 10px;
    }
    .colored {
        color: blue
    }
</style>