<template>
    <Header></Header>
    <article>
		<div class="container" role="main">
                <div class="mb-3 flex">
                    <select class="form-select" v-model="article.gubun" aria-label="Default select example">
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                    </select>
					<input type="text" class="form-control title" v-model="article.title" name="title" id="title" placeholder="  ">
                </div>

				<div class="mb-4">
					<textarea class="form-control" v-model="article.content" rows="10" name="content" id="content" placeholder="  " ></textarea>
				</div>
				<div>
                    <label for="fileUpload" type="button">
                      <img src="@/assets/fileUpload.svg" style="width:30px; margin-right:5px;"/>
                    </label>
					<input type="file" id="fileUpload" ref="fileUpload" style="width:210px;">
				</div>

			<div style="padding-top:40px; height:0px;">
				<button type="button" style="margin-right:10px;" class="btn btn btn-primary" id="btnSave" @click="saveWrite"></button>
				<button type="button" class="btn btn btn-primary" @click="backPage"></button>
			</div>
		</div>
	</article>
</template>

<script>
import { addBoard } from '@/api/index';

export default {
    data() {
        return {
            article: {
                gubun: '',
                title: '',
                content: '',
            },
        }
    },
    methods: {
        async fileHandler(e) {
            // console.log(e.target.files[0]);
            // const formData = new FormData();
            // formData.append('uploadFile', e.target.files[0]);
            // formData.append('test', '');
            
            // await axios.post('/pushImage', formData, {
            //     headers: {
            //         'Content-Type': 'multipart/form-data'
            //     }
            // });
            console.log(e)
            
        },
        backPage() {
            this.$backPage();
        },
        async saveWrite() {
            if (!this.article.gubun || !this.article.title) {
                alert(",   ");
                return;
            }

            const formData = new FormData();
            formData.append('uploadFile', this.$refs.fileUpload.files[0]);
            let param = {
                gubun: this.article.gubun,
                title: this.article.title,
                content: this.article.content,
                writer: localStorage.getItem('id'),
                recommend: '1',
                hit: '1',
            };

            /**
             * 1) 
             * Backend @RequestPart   
             *    
             * 
             *    
             * param     
             * 
             * 
             * 2)
             * @RequestPart  
             *   String   
             * stringify  
             */
            formData.append('param', JSON.stringify(param));
            await addBoard(formData).catch(error => console.log(error.message));

            this.$backPage();
        }
    }
}
</script>

<style scoped>
    .container {
  max-width: 1100px;
  margin-right: auto;
  margin-left: auto;
  margin-top : 20px;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
  padding: 1rem;
  padding-bottom: 4rem;
  border:none;
    }
    .flex {
        display: flex;
    }
    .form-select{
        width: 100px;
        height: 50px;
        margin-right:10px;
    }
    .title{
        width: 100%;
    }
</style>