<template>
  <div class="modal" v-if="isOpen" @click="closeModalOutside">

    <div class="modal-content" :style="{ width: width+'px', height: height+'px' }">
      <i class="fa-solid fa-x modal-close" @click="closeModal"></i>
      <slot></slot> <!--   slot . -->
    </div>
  </div>
</template>

<script>
export default {
  props: {
    width: Number, //    
    height: Number, //    
    clickToClose: Boolean, //  
  },
  data() {
    return {
      isOpen: false,
    };
  },
  methods: {
    modalOpen() {
      this.isOpen = true;
    },
    closeModal() {
      this.isOpen = false;
    },
    closeModalOutside(event) {
      //     .
      if (event.target.classList.contains('modal')) {
        if(this.clickToClose == true)
          this.closeModal();
      }
    },
  },
};
</script>

<style scoped>
/*   */
.modal {
  /*    */
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5); /*      */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 999; /*     */
}

.modal-content {
  /*    */
  background-color: #fff;
  padding: 20px;
  border-radius: 20px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2); /*   */
  cursor: default;
  border: #0d6efd 2px solid;

}

.modal-close{
  width: 20px;
  height: 20px;
  transform: translateX(46rem);
  background-color:transparent;
  border: none;
  cursor:pointer;
}
</style>